import React, { useState, useEffect, useRef } from "react";
import { motion, useScroll, useTransform } from "framer-motion";
import { useTheme } from "../contexts/ThemeContext";

const Hero = () => {
  const [showContent, setShowContent] = useState(false);
  const [currentWord, setCurrentWord] = useState(0);
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
  const containerRef = useRef(null);
  const { scrollY } = useScroll();
  const y = useTransform(scrollY, [0, 300], [0, -50]);
  const opacity = useTransform(scrollY, [0, 300], [1, 0]);
  const { isDark } = useTheme();

  const words = ["Full-Stack Developer", "Data Science Enthusiast", "AI/ML Developer"];

  useEffect(() => {
    const timeout = setTimeout(() => setShowContent(true), 1000);
    return () => clearTimeout(timeout);
  }, []);

  useEffect(() => {
    if (showContent) {
      const interval = setInterval(() => {
        setCurrentWord((prev) => (prev + 1) % words.length);
      }, 2000);
      return () => clearInterval(interval);
    }
  }, [showContent, words.length]);

  useEffect(() => {
    const handleMouseMove = (e) => {
      if (containerRef.current) {
        const rect = containerRef.current.getBoundingClientRect();
        setMousePosition({
          x: (e.clientX - rect.left - rect.width / 2) / 20,
          y: (e.clientY - rect.top - rect.height / 2) / 20,
        });
      }
    };

    window.addEventListener('mousemove', handleMouseMove);
    return () => window.removeEventListener('mousemove', handleMouseMove);
  }, []);

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        duration: 0.8,
        staggerChildren: 0.2
      }
    }
  };

  const itemVariants = {
    hidden: { y: 50, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: {
        duration: 0.8,
        ease: "easeOut"
      }
    }
  };

  const floatingVariants = {
    animate: {
      y: [-10, 10, -10],
      transition: {
        duration: 4,
        repeat: Infinity,
        ease: "easeInOut"
      }
    }
  };

  return (
    <motion.header 
      ref={containerRef}
      style={{ y, opacity }}
      className={`relative w-full min-h-screen flex items-center justify-center pt-24 pb-20 overflow-hidden transition-colors duration-300 ${
        isDark 
          ? 'bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900' 
          : 'bg-gradient-to-br from-white via-gray-50 to-white'
      }`}
    >
      {/* 고급 배경 장식 요소 */}
      <div className="absolute inset-0 overflow-hidden">
        <motion.div 
          className="absolute top-20 left-10 w-32 h-32 bg-[#9ECAD6]/10 rounded-full blur-xl"
          animate={{
            x: mousePosition.x,
            y: mousePosition.y,
            scale: [1, 1.2, 1],
          }}
          transition={{
            duration: 6,
            repeat: Infinity,
            ease: "easeInOut"
          }}
        />
        <motion.div 
          className="absolute bottom-20 right-10 w-40 h-40 bg-[#748DAE]/10 rounded-full blur-xl"
          animate={{
            x: -mousePosition.x * 0.5,
            y: -mousePosition.y * 0.5,
            scale: [1.2, 1, 1.2],
          }}
          transition={{
            duration: 8,
            repeat: Infinity,
            ease: "easeInOut"
          }}
        />
        <motion.div 
          className="absolute top-1/2 left-1/4 w-24 h-24 bg-[#F5CBCB]/20 rounded-full blur-lg"
          variants={floatingVariants}
          animate="animate"
        />
        <motion.div 
          className="absolute top-1/3 right-1/3 w-20 h-20 bg-[#FFEAEA]/30 rounded-full blur-lg"
          animate={{
            rotate: [0, 360],
            scale: [1, 1.1, 1],
          }}
          transition={{
            duration: 10,
            repeat: Infinity,
            ease: "linear"
          }}
        />
      </div>
      
      {/* 메인 콘텐츠 */}
      <motion.div 
        className="max-w-6xl mx-auto px-6 w-full relative z-10"
        variants={containerVariants}
        initial="hidden"
        animate="visible"
      >
        <div className="text-center">
          {/* 메인 타이틀 */}
          <motion.h1 
            className={`text-5xl md:text-6xl font-bold mb-4 transition-colors duration-300 ${
              isDark ? 'text-gray-100' : 'text-[#2D3748]'
            }`}
            variants={itemVariants}
            whileHover={{ 
              scale: 1.05,
              textShadow: isDark 
                ? "0 0 20px rgba(158, 202, 214, 0.3)" 
                : "0 0 20px rgba(116, 141, 174, 0.3)"
            }}
            transition={{ type: "spring", stiffness: 300 }}
          >
            안녕하세요, 구경선입니다
          </motion.h1>
          
          {/* 서브타이틀 */}
          <motion.div 
            className={`text-2xl font-medium mb-6 transition-colors duration-300 ${
              isDark ? 'text-gray-300' : 'text-[#4A5568]'
            }`}
            variants={itemVariants}
            key={currentWord}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            transition={{ duration: 0.5 }}
          >
            {words[currentWord]}
          </motion.div>

          {/* 설명 */}
          <motion.p 
            className={`text-lg mb-8 max-w-3xl mx-auto leading-relaxed transition-colors duration-300 ${
              isDark ? 'text-gray-300' : 'text-[#2D3748]'
            }`}
            variants={itemVariants}
            whileHover={{ 
              scale: 1.02,
              color: isDark ? "#9ECAD6" : "#748DAE"
            }}
            transition={{ type: "spring", stiffness: 300 }}
          >
            지구환경과학과와 소프트웨어학과 복수전공으로 체계적인 사고력과 기술적 역량을 겸비했습니다. 
            AI, 데이터 분석, 클라우드 인프라 분야에서 창의적 문제해결 능력을 발휘하며, 
            안정성과 사용자 경험을 동시에 고려하는 풀스택 개발자입니다. 
            기술의 완성도가 곧 서비스의 신뢰와 직결된다는 믿음으로, 
            지속 가능하고 확장 가능한 솔루션을 만드는 것을 목표로 합니다.
          </motion.p>

          {/* 고급 스킬 아이콘들 */}
          <motion.div 
            className="flex justify-center items-center space-x-6 mb-8 flex-wrap gap-4"
            variants={itemVariants}
          >
            {[
              { icon: "🤖", label: "AI/ML", gradient: "from-[#3182CE] to-[#2B6CB0]" },
              { icon: "📊", label: "Data Analysis", gradient: "from-[#38A169] to-[#2F855A]" },
              { icon: "💻", label: "Development", gradient: "from-[#D53F8C] to-[#B83280]" },
              { icon: "☁️", label: "Cloud", gradient: "from-[#805AD5] to-[#6B46C1]" },
              { icon: "🔬", label: "Research", gradient: "from-[#DD6B20] to-[#C05621]" }
            ].map((skill, index) => (
              <motion.div 
                key={skill.label}
                className="text-center"
                initial={{ opacity: 0, y: 50, rotateY: -90 }}
                animate={{ opacity: 1, y: 0, rotateY: 0 }}
                transition={{ 
                  delay: index * 0.1,
                  duration: 0.8,
                  type: "spring",
                  stiffness: 100
                }}
                whileHover={{ 
                  scale: 1.2,
                  rotateY: 10,
                  z: 50
                }}
                whileTap={{ scale: 0.95 }}
              >
                <motion.div 
                  className={`w-16 h-16 bg-gradient-to-br ${skill.gradient} rounded-full flex items-center justify-center mb-2 shadow-lg`}
                  animate={{
                    boxShadow: [
                      "0 10px 25px rgba(0,0,0,0.1)",
                      "0 20px 40px rgba(0,0,0,0.15)",
                      "0 10px 25px rgba(0,0,0,0.1)"
                    ]
                  }}
                  transition={{
                    duration: 2,
                    repeat: Infinity,
                    ease: "easeInOut"
                  }}
                >
                  <span className="text-2xl">{skill.icon}</span>
                </motion.div>
                <span className={`text-sm font-semibold transition-colors duration-300 ${
                  isDark ? 'text-gray-300' : 'text-[#2D3748]'
                }`}>{skill.label}</span>
              </motion.div>
            ))}
          </motion.div>

          {/* 고급 CTA 버튼들 */}
          <motion.div 
            className="flex flex-col sm:flex-row justify-center gap-4"
            variants={itemVariants}
          >
            <motion.a
              href="#projects"
              className="inline-flex items-center justify-center px-8 py-3 bg-gradient-to-r from-[#3182CE] to-[#2B6CB0] text-white rounded-lg font-semibold shadow-lg"
              whileHover={{ 
                scale: 1.05,
                boxShadow: "0 20px 40px rgba(49, 130, 206, 0.3)",
                y: -2
              }}
              whileTap={{ scale: 0.95 }}
              transition={{ type: "spring", stiffness: 400, damping: 17 }}
            >
              프로젝트 보기
            </motion.a>
            <motion.a
              href="#contact"
              className="inline-flex items-center justify-center px-8 py-3 border-2 border-[#3182CE] text-[#3182CE] rounded-lg font-semibold shadow-lg"
              whileHover={{ 
                scale: 1.05,
                backgroundColor: "#3182CE",
                color: "white",
                boxShadow: "0 20px 40px rgba(49, 130, 206, 0.3)",
                y: -2
              }}
              whileTap={{ scale: 0.95 }}
              transition={{ type: "spring", stiffness: 400, damping: 17 }}
            >
              연락하기
            </motion.a>
          </motion.div>
        </div>
      </motion.div>
    </motion.header>
  );
};

export default Hero;